library.add ( faMinus );
library.add ( faTimes );

export default {

    // Module/component name
    name: 'VueGridDesigner',

    mixins: [
        PropsMixin,
        ComponentMixin
    ],

    components: {
        FontAwesomeIcon
    }

};
